form{
    background-color:rgba(200,200,200,0.6);
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    font-size:4vmin;
    margin: 3vmin;
    padding:5px;
    min-width: 80%;
    border-radius: 10px;

}
h1{
    font-size: 7vmin;
  
}
.formInput{
    display: flex;
    align-items: center;
    flex-direction: column;
    margin: 3vmin;
    padding: 3vmin;
}
li input{
    font-size: 5vmin;
}
select,input{
    height: 5vmin;
    margin: 1vmin;
    border-radius: 10px;

}
::placeholder{
    font-size: 3vmin;
}
#getBtn{
    font-size: 5vmin;
   align-self:center;
    padding: 10px;
}